<template>
  <el-card class="main-card">
    <div class="title">{{ this.$route.name }}</div>
    <div class="review-menu">
      <span></span>
      <span
        @click="changeReview(null)"
        :class="isReview == null ? 'active-review' : 'review'"
      >
        
      </span>
      <span
        @click="changeReview(1)"
        :class="isReview == 1 ? 'active-review' : 'review'"
      >
        
      </span>
      <span
        @click="changeReview(0)"
        :class="isReview == 0 ? 'active-review' : 'review'"
      >
        
      </span>
    </div>
    <!--  -->
    <div class="operation-container">
      <el-button
        type="danger"
        size="small"
        icon="el-icon-delete"
        :disabled="commentIdList.length == 0"
        @click="remove = true"
      >
        
      </el-button>
      <el-button
        type="success"
        size="small"
        icon="el-icon-success"
        :disabled="commentIdList.length == 0"
        @click="updateCommentReview(null)"
      >
        
      </el-button>
      <!--  -->
      <div style="margin-left:auto">
        <el-select
          clearable
          v-model="type"
          placeholder=""
          size="small"
          style="margin-right:1rem"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
        <el-input
          v-model="keywords"
          prefix-icon="el-icon-search"
          size="small"
          placeholder=""
          style="width:200px"
          @keyup.enter.native="searchComments"
        />
        <el-button
          type="primary"
          size="small"
          icon="el-icon-search"
          style="margin-left:1rem"
          @click="searchComments"
        >
          
        </el-button>
      </div>
    </div>
    <!--  -->
    <el-table
      border
      :data="commentList"
      @selection-change="selectionChange"
      v-loading="loading"
    >
      <!--  -->
      <el-table-column type="selection" width="55" />
      <el-table-column prop="avatar" label="" align="center" width="120">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" width="40" height="40" />
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column
        prop="nickname"
        label=""
        align="center"
        width="120"
      />
      <!--  -->
      <el-table-column
        prop="replyNickname"
        label=""
        align="center"
        width="120"
      >
        <template slot-scope="scope">
          <span v-if="scope.row.replyNickname">
            {{ scope.row.replyNickname }}
          </span>
          <span v-else></span>
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column prop="articleTitle" label="" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.articleTitle">
            {{ scope.row.articleTitle }}
          </span>
          <span v-else></span>
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column prop="commentContent" label="" align="center">
        <template slot-scope="scope">
          <span v-html="scope.row.commentContent" class="comment-content" />
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column
        prop="createTime"
        label=""
        width="150"
        align="center"
      >
        <template slot-scope="scope">
          <i class="el-icon-time" style="margin-right:5px" />
          {{ scope.row.createTime | date }}
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column prop="isReview" label="" width="80" align="center">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.isReview == 0" type="warning"></el-tag>
          <el-tag v-if="scope.row.isReview == 1" type="success"></el-tag>
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column label="" align="center" width="100">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.type == 1"></el-tag>
          <el-tag v-if="scope.row.type == 2" type="warning"></el-tag>
          <el-tag v-if="scope.row.type == 3" type="danger"></el-tag>
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column label="" width="160" align="center">
        <template slot-scope="scope">
          <el-button
            v-if="scope.row.isReview == 0"
            size="mini"
            type="success"
            slot="reference"
            @click="updateCommentReview(scope.row.id)"
          >
            
          </el-button>
          <el-popconfirm
            style="margin-left:10px"
            title=""
            @confirm="deleteComments(scope.row.id)"
          >
            <el-button size="mini" type="danger" slot="reference">
              
            </el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      class="pagination-container"
      background
      @size-change="sizeChange"
      @current-change="currentChange"
      :current-page="current"
      :page-size="size"
      :total="count"
      :page-sizes="[10, 20]"
      layout="total, sizes, prev, pager, next, jumper"
    />
    <!--  -->
    <el-dialog :visible.sync="remove" width="30%">
      <div class="dialog-title-container" slot="title">
        <i class="el-icon-warning" style="color:#ff9900" />
      </div>
      <div style="font-size:1rem"></div>
      <div slot="footer">
        <el-button @click="remove = false"> </el-button>
        <el-button type="primary" @click="deleteComments(null)">
           
        </el-button>
      </div>
    </el-dialog>
  </el-card>
</template>

<script>
export default {
  created() {
    this.listComments();
  },
  data: function() {
    return {
      loading: true,
      remove: false,
      options: [
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        },
        {
          value: 3,
          label: ""
        }
      ],
      commentList: [],
      commentIdList: [],
      type: null,
      keywords: null,
      isReview: null,
      current: 1,
      size: 10,
      count: 0
    };
  },
  methods: {
    selectionChange(commentList) {
      this.commentIdList = [];
      commentList.forEach(item => {
        this.commentIdList.push(item.id);
      });
    },
    searchComments() {
      this.current = 1;
      this.listComments();
    },
    sizeChange(size) {
      this.size = size;
      this.listComments();
    },
    currentChange(current) {
      this.current = current;
      this.listComments();
    },
    changeReview(review) {
      this.current = 1;
      this.isReview = review;
    },
    updateCommentReview(id) {
      let param = {};
      if (id != null) {
        param.idList = [id];
      } else {
        param.idList = this.commentIdList;
      }
      param.isReview = 1;
      this.axios.put("/api/admin/comments/review", param).then(({ data }) => {
        if (data.flag) {
          this.$notify.success({
            title: "",
            message: data.message
          });
          this.listComments();
        } else {
          this.$notify.error({
            title: "",
            message: data.message
          });
        }
      });
    },
    deleteComments(id) {
      var param = {};
      if (id == null) {
        param = { data: this.commentIdList };
      } else {
        param = { data: [id] };
      }
      this.axios.delete("/api/admin/comments", param).then(({ data }) => {
        if (data.flag) {
          this.$notify.success({
            title: "",
            message: data.message
          });
          this.listComments();
        } else {
          this.$notify.error({
            title: "",
            message: data.message
          });
        }
        this.remove = false;
      });
    },
    listComments() {
      this.axios
        .get("/api/admin/comments", {
          params: {
            current: this.current,
            size: this.size,
            keywords: this.keywords,
            type: this.type,
            isReview: this.isReview
          }
        })
        .then(({ data }) => {
          this.commentList = data.data.recordList;
          this.count = data.data.count;
          this.loading = false;
        });
    }
  },
  watch: {
    isReview() {
      this.current = 1;
      this.listComments();
    },
    type() {
      this.current = 1;
      this.listComments();
    }
  }
};
</script>

<style scoped>
.comment-content {
  display: inline-block;
}
.operation-container {
  margin-top: 1.5rem;
}
.review-menu {
  font-size: 14px;
  margin-top: 40px;
  color: #999;
}
.review-menu span {
  margin-right: 24px;
}
.review {
  cursor: pointer;
}
.active-review {
  cursor: pointer;
  color: #333;
  font-weight: bold;
}
</style>
